}

.search-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  background-color: #dddddd;
  border-radius: 100%;
  cursor: pointer;
}

.search-icon::before {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  background-image: url("C:\Users\Ankitha K\OneDrive\Desktop\React\card-listing\public\searchbutton.png"); /* Replace with your search icon image path */
  background-size: cover;
}

.search-section {
  display: flex;
  align-items: center;
}

.search-section input {
  padding: 8px;
  margin-right: 8px;
  border: 1px solid #dddddd;
  border-radius: 4px;
}

.search-button {
  padding: 8px 16px;
  background-color: #4caf50;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.search-button:hover {
  background-color: #45a049;
}

.all-cards-tab {
  display: flex;
  flex-wrap: wrap;
  gap:30px;
  justify-content: center;
}

.card-item {
  border: 1px solid #dacdcd;
  border-radius: 8px;
  padding: 10px;
  width: calc(28.33% - 20px);
  left: 50px;
  margin-bottom: 20px;
}

.card-details {
  margin-bottom: 10px;
}

.card-name {
  font-weight: bold;
  color:#0b0000
}

.budget-name {
  color: #888888;
}

.card-table {
  width: 100%;
  margin-top: 10px;
}
.word-value{
  width:100%;
  margin-right: 10px;
  margin-left: 10px;
}
.card-table th,
.card-table td {
  padding: 8px;
  text-align: center;
}

.card-table th {
  color:#766d6d;
  background-color: #f7f3f3;
}

.card-table td {
  color: #444444;
}




